    <div class="row mb-3">
        <div class="col">
            <h5> {{ id }}</h5>
            <Line :chart-data="chartData" />
        </div>
    </div>
</template>

<script>
import { Line } from 'vue-chartjs';
import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  LineElement,
  LinearScale,
  PointElement,
  CategoryScale,
} from 'chart.js'

